import React, { useRef, useEffect, useState } from "react";
import { Header } from "../components/Header";
import { FooterLogin } from "../components/FooterLogin";
import axios from "axios";
import { useNavigate } from "react-router-dom";
import SuccessPop from "../components/SuccessPop";
import ErrorPop from "../components/ErrorPop";
import { Link } from "react-router-dom";
import { useTranslation } from "react-i18next";
function OtpVerify() {
  const [err_email, seterr_email] = useState("");
  const [err_pass, seterr_pass] = useState("");
  const [email, setEmail] = useState("");
  const [password, setOTP] = useState("");
  const [gender, setGender] = useState("");
  const [err_gender, seterr_gender] = useState("");
  const [rememberMe, setRememberMe] = useState(false);
  const [showPopSuccess, setShowPopSuccess] = useState(false);
  const [showPopError, setShowPopError] = useState(false);
  const [successPopMessage, setPopsuccessMessage] = useState("");
  const [errorPopMessage, setPoperrorMessage] = useState("");
  const [IpAddress, setIpAddress] = useState("");
  const [LocationData, setLocationData] = useState([]);
  const [setotpEmail, otpEmail] = useState("");
  const [setlat, latt] = useState("");
  const [setlong, longg] = useState("");
  const successRef = useRef(null);
  const errorRef = useRef(null);
  const navigate = useNavigate();
  const { t, i18n } = useTranslation();
  useEffect(() => {
    // Check if the username key exists in session storage
    const storedUsername = localStorage.getItem("userLogin");
    const userLogin = JSON.parse(storedUsername);
    if (userLogin != null) {
      navigate("/dashboard");
    }
  }, []);
  useEffect(() => {
    // Check if the username key exists in session storage
    const Otpemail = localStorage.getItem("Otpemail");
    setEmail(Otpemail);
    if (Otpemail === null) {
      navigate("/login");
    }
  }, []);
  useEffect(() => {
    // Fetch IP address from ipify API
    const getIpAddress = async () => {
      try {
        const response = await axios.get("https://api.ipify.org?format=json");

        setIpAddress(response.data.ip); // Update state with IP address
      } catch (error) {
        console.error("Error fetching IP address:", error);
      }
    };

    getIpAddress();
  }, []);
  const getCityFromGPS = async () => {
    if ("geolocation" in navigator) {
      navigator.geolocation.getCurrentPosition(
        async (position) => {
          const lat = position.coords.latitude;
          const lon = position.coords.longitude;
          //setlat(lat);
          //setlong(lon);
          const response = await fetch(
            `https://nominatim.openstreetmap.org/reverse?format=json&lat=${lat}&lon=${lon}`
          );
          const data = await response.json();

          console.log(data, "kl");
          let addressParts = {
            city: data.address.state_district,
            country_code: data.address.country_code,
            region: data.display_name,
            country_name: data.address.country,
            postal: data.address.postcode,
            timezone: "",
            latitude: data.lat,
            longitude: data.lon,
          };
          setLocationData(addressParts);
          console.log(data, "kl");

          // const formattedAddress = addressParts
          //   .filter((part) => part !== "")
          //   .join(",");

          //setCity(formattedAddress);
        },
        (error) => {
          getLocationDetails();
          console.error("Error getting location:", error);
        }
      );
    } else {
      console.log("Geolocation is not supported by this browser.");
    }
  };
  const getLocationDetails = async () => {
    try {
      // Step 1: Get the user's public IP address
      const ipResponse = await axios.get("https://api.ipify.org?format=json");
      const ip = ipResponse.data.ip;

      // Step 2: Use ipapi to get latitude and longitude based on IP address
      const geoResponse = await axios.get(`https://ipapi.co/${ip}/json/`);
      console.log(geoResponse.data);
      setLocationData(geoResponse.data);

      // Set the location details
    } catch (error) {
      console.error("Error fetching location data:", error);
    }
  };

  useEffect(() => {
    //getLocationDetails(); // Fetch location details on component mount
    getCityFromGPS();
  }, []);

  var apiURL = "https://backend.amourette.no/api/";
  const handleSubmit = async (event) => {
    event.preventDefault(); // Prevent default form submission

    // Validate email and password
    if (!email || !password) {
      alert("Please fill out all required fields.");
      return;
    }

    // if (rememberMe) {
    //   localStorage.setItem("email", email);
    //   localStorage.setItem("password", password); // Store password (caution: storing passwords in localStorage is not recommended for real applications)
    // } else {
    //   localStorage.removeItem("email");
    //   localStorage.removeItem("password");
    // }

    const formData = new FormData();
    formData.append("email", email);
    formData.append("otp", password);
    formData.append("IpAddress", IpAddress);
    formData.append("city", LocationData.city);
    formData.append("country_code", LocationData.country_code);
    formData.append("country_name", LocationData.country_name);
    formData.append("region", LocationData.region);
    formData.append("postal", LocationData.postal);
    formData.append("timezone", LocationData.timezone);
    formData.append("latitude", LocationData.latitude);
    formData.append("longitude", LocationData.longitude);

    try {
      const res = await axios.post(apiURL + "login/checkloginOTP", formData, {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
        },
      });
      console.log(res.data);
      setShowPopSuccess(true);
      setPopsuccessMessage(res.data.message);

      var userResp = res.data.user;
      let userData = {
        id: userResp.id,
        email: userResp.email,
        status: userResp.status,
        token: userResp.token,
      };
      console.log();
      console.log(userData);
      localStorage.setItem("userLogin", JSON.stringify(userData));
      setTimeout(() => {
        setShowPopError(false);
        setShowPopSuccess(false);
        navigate("/dashboard");
      }, 1500);

      // Handle success (e.g., redirect or show a success message)
    } catch (err) {
      console.log("dd");

      if (err.response && err.response.data && err.response.data.message) {
        setShowPopSuccess(false);
        setShowPopError(true);

        setPoperrorMessage(err.response.data.message); // Store the error message in state
      } else {
        setShowPopSuccess(false);
        setShowPopError(true);

        setPoperrorMessage("An unknown error occurred.");
      }

      setTimeout(() => {
        setShowPopError(false);
        setShowPopSuccess(false);
      }, 2500);
    }
  };
  return (
    <>
      <Header />
      {showPopSuccess && <SuccessPop message={successPopMessage} />}
      {showPopError && <ErrorPop message={errorPopMessage} />}
      <section className="regblock login_registermain d-block">
        <div className="container-lg">
          <div className="row justify-content-start">
            <div className="col-md-6 p-0">
              <div className="regform d-flex flex-column gap-4">
                <form onSubmit={handleSubmit} method="post">
                  {/* 1st step */}
                  <div className="stepbox">
                    <div className="d-flex flex-column gap-3 regtitle mb-5">
                      <div className="d-flex justify-content-between">
                        <h1>{t("VerifyOTP")}</h1>
                      </div>
                    </div>
                    <div className="row gy-4">
                      <div className="col-12">
                        <div className="d-flex flex-column gap-2">
                          <label htmlFor="">{t("E-mail")}</label>
                          <input
                            type="email"
                            required
                            placeholder={t("youE-mail")}
                            name="email"
                            value={email}
                            onChange={(e) => setEmail(e.target.value)}
                          />
                        </div>
                      </div>
                      <div className="col-12">
                        <div className="d-flex flex-column gap-2">
                          <label htmlFor="">{t("OTP")}</label>
                          <input
                            type="text"
                            required
                            placeholder={t("Enterotp")}
                            value={password}
                            onChange={(e) => setOTP(e.target.value)}
                            name="password"
                          />
                        </div>
                      </div>

                      <div className="col-12">
                        <button type="submit" className="regbtnsubmit">
                          {t("VerifyOTP")}
                        </button>
                      </div>
                    </div>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
      </section>
      <FooterLogin />
    </>
  );
}

export default OtpVerify;
